<template>
    <div class="main">
        <div class="studentManagement">
            <div class="searchA">
                <el-select clearable size="small" placeholder="请选择年级" v-model="search.grade_id" @change="gradeChange">
                    <el-option v-for="item in gradeList" :label="item.grade_name" :value="item.id" :key="item.id"></el-option>
                </el-select>
                <el-select clearable size="small" placeholder="请选择班级" v-model="search.class_id">
                    <el-option v-for="item in classList" :label="item.class_name" :value="item.id" :key="item.id"></el-option>
                </el-select>
                <el-input style="width:300px" placeholder="学生姓名、拼音、身份证、联系电话" v-model="search.keyword" clearable size="small"></el-input>
                <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" @click="addBtn()">新增</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" :max-height="tableHeight" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true" v-if="item.prop == 'disabled'">
                            <template slot-scope="scope">
                                <el-tag size="small" :type="scope.row.disabled==0?'':'info'">{{scope.row.disabled==0?'正常':'禁用'}}</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :min-width="item.minWidth" v-else></el-table-column>
                    </template>
                    <el-table-column prop="address" label="操作" fixed="right" width="120">
                        <template slot-scope="scope">
                            <el-button @click="editBtn(scope.row)" type="text" size="small">编辑</el-button>
                            <el-button @click="delBtn(scope.row.id)" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagination">
                    <el-pagination background :current-page.sync="pageNum" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
                </div>
            </div>
            <!-- 新增编辑弹窗 -->
            <el-dialog :title="title" v-dialogDrag :visible.sync="dialogVisible" width="600px" :close-on-click-modal="false">
                <el-form :model="formData" size="medium" ref="formData" :rules="rule" label-width="110px">
                    <el-form-item label="学生姓名" prop="student_name">
                        <el-input v-model.trim="formData.student_name" placeholder="请输入学生姓名"></el-input>
                    </el-form-item>
                    <el-form-item label="性别" prop="sex">
                        <el-radio-group v-model="formData.sex">
                            <el-radio :label="1">男</el-radio>
                            <el-radio :label="2">女</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="所在年级" prop="grade_id">
                        <el-select v-model="formData.grade_id" placeholder="请选择所在年级" @change="gradeC">
                            <el-option v-for="item in gradeList" :label="item.grade_name" :value="item.id" :key="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="所在班级" prop="class_id">
                        <el-select v-model="formData.class_id" placeholder="请选择所在班级">
                            <el-option v-for="item in classList" :label="item.class_name" :value="item.id" :key="item.id"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="学籍号">
                        <el-input v-model.trim="formData.student_code" placeholder="请输入学籍号"></el-input>
                    </el-form-item>
                    <el-form-item label="身份证号">
                        <el-input v-model.trim="formData.id_card" placeholder="请输入身份证号"></el-input>
                    </el-form-item>
                    <el-form-item label="SN号">
                        <el-input v-model.trim="formData.sn_code" placeholder="请输入SN号"></el-input>
                    </el-form-item>
                    <el-form-item label="M1卡号">
                        <el-input v-model.trim="formData.m1_code" placeholder="请输入M1卡号"></el-input>
                    </el-form-item>
                    <el-form-item label="家长姓名">
                        <el-input v-model.trim="formData.guardian_name" placeholder="请输入家长姓名"></el-input>
                    </el-form-item>
                    <el-form-item label="联系电话">
                        <el-input v-model.trim="formData.guardian_tel" placeholder="请输入联系电话"></el-input>
                    </el-form-item>
                    <el-form-item label="住址">
                        <el-input v-model.trim="formData.address" placeholder="请输入住址"></el-input>
                    </el-form-item>
                    <el-form-item label="学生状态" prop="disabled">
                        <el-switch v-model="formData.disabled"></el-switch>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="submit">确 定</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    studentManagementList,
    studentManagementGrades,
    studentManagementClass,
    studentManagementInfo,
    studentManagementAdd,
    studentManagementEdit,
    studentManagementDel,
    hashApi,
} from '@/api/index';

export default {
    data() {
        return {
            total: 0,
            pageNum: 1,
            pageSize: 15,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            formData: {
                student_name: "",
                sex: 1,
                grade_id: "",
                class_id: "",
                student_code: "",
                id_card: "",
                sn_code: "",
                m1_code: "",
                guardian_name: "",
                guardian_tel: "",
                address: "",
                disabled: true,
            },
            method_auth: {},
            editId: '',
            title: '',
            search: {
                grade_id: '',
                class_id: '',
                keyword: '',
            },
            dialogVisible: false,
            rule: {
                student_name: [
                    { required: true, message: '请输入学生姓名', trigger: 'blur' },
                ],
                sex: [
                    { required: true, message: '请选择性别', trigger: 'change' }
                ],
                grade_id: [
                    { required: true, message: '请选择所在年级', trigger: 'change' }
                ],
                class_id: [
                    { required: true, message: '请选择所在班级', trigger: 'change' }
                ],
                disabled: [
                    { required: true, message: '请选择学生状态', trigger: 'change' }
                ],
            },
            tableHead: [
                { title: 'ID', prop: 'id', minWidth: '100', },
                { title: '学生姓名', prop: 'student_name', minWidth: '120', },
                { title: '性别', prop: 'sex_name', minWidth: '100', },
                { title: '班级', prop: 'class_name', minWidth: '100', },
                { title: '身份证号', prop: 'id_card', minWidth: '180', },
                { title: 'SN号', prop: 'sn_code', minWidth: '150', },
                { title: 'M1卡号', prop: 'm1_code', minWidth: '150', },
                { title: '学籍号', prop: 'student_code', minWidth: '120', },
                { title: '家长姓名', prop: 'guardian_name', minWidth: '120', },
                { title: '联系电话', prop: 'guardian_tel', minWidth: '150', },
                { title: '住址', prop: 'address', minWidth: '150', },
                { title: '状态', prop: 'disabled', minWidth: '100', },
            ],
            tableData: [],
            hash: '',
            gradeList: [],
            classList: [],
        };
    },
    mounted() {
        this.getList();
        this.Grade();
        this.getHash();
    },
    methods: {
        gradeChange(e) {
            if (String(e).length > 0) {
                this.Class(e);
            }
        },
        gradeC(e) {
            if (String(e).length > 0) {
                this.Class(e);
            }
            this.formData.class_id = '';
        },
        serchList() {
            this.pageNum = 1;
            this.pageSize = 15;
            this.getList()
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.pageNum = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.pageNum = newPage;
            this.getList();
        },
        async Grade() {
            const res = await this.axios.post(studentManagementGrades);
            if (res.data.code == 1) {
                this.gradeList = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async Class(id) {
            const res = await this.axios.post(studentManagementClass, { grade_id: id, });
            if (res.data.code == 1) {
                this.classList = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getList() {
            let data = {
                curr: this.pageNum,
                pageSize: this.pageSize,
                keyword: this.search.keyword,
                grade_id: this.search.grade_id,
                class_id: this.search.class_id,
            };
            const res = await this.axios.post(studentManagementList, data);
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //获取Hash
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'studentManagement' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                });
            }
        },
        addBtn() {
            this.title = '新增学生';
            this.dialogVisible = true;
            this.resetForm();
        },
        editBtn(row) {
            this.title = '编辑学生';
            this.dialogVisible = true;
            this.resetForm();
            this.$nextTick(() => {
                this.getInfo(row.id);
            })
        },
        async getInfo(id) {
            const res = await this.axios.post(studentManagementInfo, { id: id });
            if (res.data.code == 1) {
                if (res.data.data.disabled == 0) {
                    res.data.data.disabled = true
                } else if (res.data.data.disabled == 1) {
                    res.data.data.disabled = false
                }
                this.formData = res.data.data;
                this.Class(res.data.data.grade_id);
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        resetForm() {
            this.formData = {
                student_name: "",
                sex: 1,
                grade_id: "",
                class_id: "",
                student_code: "",
                id_card: "",
                sn_code: "",
                m1_code: "",
                guardian_name: "",
                guardian_tel: "",
                address: "",
                disabled: true,
            };
            this.$nextTick(() => {
                this.$refs['formData'].resetFields();
            })
        },
        submit() {
            this.$refs['formData'].validate(async valid => {
                if (valid) {
                    await this.getHash();
                    let data = {
                        student_name: this.formData.student_name,
                        sex: this.formData.sex,
                        hash: this.hash,
                        grade_id: this.formData.grade_id,
                        class_id: this.formData.class_id,
                        student_code: this.formData.student_code,
                        id_card: this.formData.id_card,
                        sn_code: this.formData.sn_code,
                        m1_code: this.formData.m1_code,
                        guardian_name: this.formData.guardian_name,
                        guardian_tel: this.formData.guardian_tel,
                        address: this.formData.address,
                        disabled: this.formData.disabled == true ? 0 : 1,
                    }
                    let api;
                    if (this.title == '新增学生') {
                        api = studentManagementAdd
                    } else {
                        api = studentManagementEdit;
                        data.id = this.formData.id;
                    }
                    const res = await this.axios.post(api, data);
                    if (res.data.code == 1) {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.dialogVisible = false;
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }
                    this.getList();
                    this.getHash();
                }
            })
        },
        delBtn(id) {
            this.$confirm('是否确定删除该设备', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(() => {
                    const datas = {
                        id: id,
                    };
                    const res = this.axios.post(studentManagementDel, datas).then(res => {
                        if (res.data.code == 1) {
                            this.$notify.success({
                                showClose: true,
                                message: res.data.msg,
                                type: 'success'
                            });
                            if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                                this.page -= 1;
                            }
                            this.getList();
                        } else {
                            this.$notify.error({
                                title: '失败',
                                message: res.data.msg
                            });
                        }
                    });
                    this.getHash();
                })
                .catch(err => {
                    // this.$notify.info({
                    //     title: '取消',
                    //     message: '取消操作'
                    // });
                });
        },
    }
};
</script>

<style >
.studentManagement .el-table__row--level-1 {
    background: #fdfafa !important ;
}
</style>
<style lang="scss" scoped>
.studentManagement {
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
        .searchBtn {
            border: solid 1px #2f39ff;
            color: #2f39ff;
        }
    }
    .btn {
        width: 250px;
    }
    /deep/ .el-dialog {
        margin-top: 5vh !important;
    }
}
</style>



